.collections__catalog {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 36px;
  // align-items: flex-start;
  max-width: 1105px;
}

.collections__column {
  display: flex;
  flex-wrap: wrap;
  // gap: 5px;
  background-color: #1c1c1e;
}

.collections__item {
  display: flex;
  justify-content: center;
  background-color: #1c1c1e;
  align-items: center;
  padding: 20px;

  &--full {
    width: 100%;
  }

  &--half {
    width: 50%;
    position: relative;
    align-self: flex-start;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 18px;
      height: 18px;
      border-top: 1px solid #916d47;
      border-left: 1px solid #916d47;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      right: 0;
      display: block;
      width: 18px;
      height: 18px;
      border-right: 1px solid #916d47;
      border-bottom: 1px solid #916d47;
    }
  }

  &--third {
    width: 32%;
  }

  &--six {
    width: 66%;
  }

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 20px;
  }

  img {
    max-width: 100%;
    height: auto;
  }
}

.collection__head {
  display: grid;
  grid-template-columns: max-content max-content;
  column-gap: 28px;
}

.collection__title {
  @include title;
  grid-column: 1/2;
}

.collection__text {
  @include sectiontext;
  grid-column: 1/2;

  p {
    max-width: 490px;
  }
}

.collection__table {
  grid-row: 1/3;
  grid-column: 2/3;
  padding-top: 30px;

  font-weight: 300;
  font-size: 14px;
  line-height: 23px;

  table {
    tr {
      td {
        font-weight: 400;
        &:first-child {
          font-family: $base-light-font;
        }
      }
    }
  }
}
